import React, { useEffect, useState } from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';
import { InsertBook } from '../SQLite/Books';
import { useSQLite } from 'react-sqlite-hook';

const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 300,
    height: 500,
    bgcolor: 'background.paper',
    // border: '2px solid #000',
    borderRadius: '12px',
    boxShadow: 24,
    p: 4,
    overflow: 'scroll !important'
};

export const BasicModal = ({ openModal, setOpenModal, src, title, authors, description }) => {

    const [isAddingBook, setIsAddingBook] = useState(false);
    const { db } = useSQLite({
        dbName: 'libraryDB',
        version: '1.0'
    });

    const handleAdd = async () => {
        try {
            if (db) {
                console.log("entro en el handleInser");
                await db.executeSql('INSERT INTO books (bookName, author, description, imgSource) VALUES (?, ?, ?, ?)',
                    [title, authors, description, src]
                );
                console.log("termino el handleInser");                
            }
        } catch (error) {
            console.error('Error al insertar el libro:', error);
        }
    }

    return (
        <div>
            <Modal
                open={openModal}
                onClose={() => setOpenModal(!openModal)}
                aria-labelledby="modal-modal-title"
                aria-describedby="modal-modal-description"
            >
                <Box sx={style}>
                    <div className='card' >
                        <img src={src} />
                        <div className="bottom">
                            <Button color='error' variant='contained' onClick={handleAdd}>Agregar a biblioteca</Button>
                            <h3 className='cardTitle'>{title}</h3>
                            <p className="cardAuthors">{authors}</p>
                            <p className="cardDescription">{description}</p>
                        </div>
                    </div>
                </Box>
            </Modal>       
        </div>
    );
}